import React from 'react';
import type { Heading } from './utils/toc';

interface TableOfContentsProps {
    headings: Heading[];
}

export const TableOfContents: React.FC<TableOfContentsProps> = ({ headings }) => {
    if (headings.length === 0) {
        return null;
    }

    return (
        <div className="p-6 bg-brand-pink-light/50 border border-brand-border rounded-xl mb-8">
            <h2 className="font-serif text-2xl font-bold text-brand-text-title mb-4">Table of Contents</h2>
            <ul className="space-y-2">
                {headings.map((heading) => (
                    <li key={heading.id} style={{ marginLeft: `${(heading.level - 2) * 1.5}rem` }}>
                        <a 
                            href={`#${heading.id}`} 
                            className="text-brand-text-body hover:text-brand-pink hover:underline transition-colors"
                        >
                            {heading.text}
                        </a>
                    </li>
                ))}
            </ul>
        </div>
    );
};
