import React, { useState, useMemo } from 'react';
import { BlogPost } from '../data/blogPosts';
import { useRouter } from 'next/navigation';

interface RecipeIndexProps {
    posts: BlogPost[];
}

export const RecipeIndex: React.FC<RecipeIndexProps> = ({ posts }) => {
    const router = useRouter();
    const [selectedCategory, setSelectedCategory] = useState<string>('All');

    // Extract unique categories
    const categories = useMemo(() => {
        const cats = new Set(posts.map(post => post.category).filter(Boolean));
        return ['All', ...Array.from(cats).sort()];
    }, [posts]);

    // Filter posts
    const filteredPosts = useMemo(() => {
        if (selectedCategory === 'All') return posts;
        return posts.filter(post => post.category === selectedCategory);
    }, [posts, selectedCategory]);

    return (
        <div className="container mx-auto px-4 py-8">
            <h1 className="text-4xl font-serif font-bold text-brand-text-title mb-8 text-center">
                Recipe Index
            </h1>

            {/* Filter Bar */}
            <div className="flex flex-wrap justify-center gap-4 mb-12">
                {categories.map(category => (
                    <button
                        key={category}
                        onClick={() => setSelectedCategory(category)}
                        className={`px-6 py-2 rounded-full text-sm font-bold transition-all duration-300 transform hover:scale-105
              ${selectedCategory === category
                                ? 'bg-brand-pink text-white shadow-lg'
                                : 'bg-white text-brand-text-body border border-brand-border hover:border-brand-pink hover:text-brand-pink'
                            }`}
                    >
                        {category}
                    </button>
                ))}
            </div>

            {/* Recipe Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                {filteredPosts.map(post => (
                    <div
                        key={post.id}
                        onClick={() => router.push(`/recipes/${post.slug}`)}
                        className="group cursor-pointer bg-[#FFC0CB] rounded-xl shadow-md overflow-hidden hover:shadow-xl transition-all duration-300 border border-transparent hover:border-brand-pink/20 h-80 relative flex flex-col items-center justify-center p-6 text-center"
                    >
                        <div className="absolute top-4 left-4">
                            <span className="px-4 py-1.5 bg-white text-[#FFC0CB] text-sm font-bold rounded-full shadow-sm">
                                {post.category}
                            </span>
                        </div>

                        <h3 className="text-3xl font-serif font-bold text-white drop-shadow-sm px-4">
                            {post.title.split('|')[0].trim()}
                        </h3>
                    </div>
                ))}
            </div>

            {filteredPosts.length === 0 && (
                <div className="text-center py-12">
                    <p className="text-xl text-gray-500">No recipes found in this category.</p>
                </div>
            )}
        </div>
    );
};
