import React, { useEffect, useState } from 'react';
import { XIcon, ArrowLeftIcon, ChevronRightIcon } from './Icons';
import AdSenseAd from './AdSenseAd';

interface ImageModalProps {
    imageUrl: string;
    onClose: () => void;
    onPrev: () => void;
    onNext: () => void;
    currentIndex?: number;
    totalImages?: number;
}

export const ImageModal: React.FC<ImageModalProps> = ({ imageUrl, onClose, onPrev, onNext, currentIndex = 0, totalImages = 0 }) => {
    const [showAd, setShowAd] = useState(true);
    const [countdown, setCountdown] = useState(5);

    useEffect(() => {
        const handleKeyDown = (e: KeyboardEvent) => {
            if (e.key === 'Escape') onClose();
            if (!showAd) {
                if (e.key === 'ArrowLeft') onPrev();
                if (e.key === 'ArrowRight') onNext();
            }
        };
        window.addEventListener('keydown', handleKeyDown);
        return () => window.removeEventListener('keydown', handleKeyDown);
    }, [onClose, onPrev, onNext, showAd]);

    useEffect(() => {
        if (showAd && countdown > 0) {
            const timer = setTimeout(() => setCountdown(countdown - 1), 1000);
            return () => clearTimeout(timer);
        } else if (countdown === 0) {
            setShowAd(false);
        }
    }, [showAd, countdown]);

    const handleSkipAd = () => {
        setShowAd(false);
    };

    return (
        <div className="fixed inset-0 bg-black/80 z-50 flex items-center justify-center p-4 backdrop-blur-sm fade-in" onClick={onClose}>
            <button onClick={onClose} className="absolute top-4 right-4 text-white hover:text-gray-300 transition-colors z-10"><XIcon className="w-10 h-10" /></button>

            {/* Pagination Counter */}
            {!showAd && totalImages > 0 && (
                <div className="absolute top-4 left-1/2 -translate-x-1/2 bg-black/60 text-white px-4 py-2 rounded-full font-semibold">
                    {currentIndex + 1} / {totalImages}
                </div>
            )}

            {showAd ? (
                <div className="bg-white rounded-lg p-8 max-w-md w-full text-center" onClick={e => e.stopPropagation()}>
                    <h3 className="text-xl font-bold mb-4 text-brand-text-title">Please wait {countdown} seconds</h3>
                    <AdSenseAd />
                    <p className="text-sm text-gray-600 mt-4">Your image will appear in {countdown} second{countdown !== 1 ? 's' : ''}</p>
                    {countdown === 0 && (
                        <button
                            onClick={handleSkipAd}
                            className="mt-4 px-6 py-2 bg-brand-pink text-white rounded-full font-bold hover:opacity-90 transition-opacity"
                        >
                            View Image
                        </button>
                    )}
                </div>
            ) : (
                <>
                    <button onClick={(e) => { e.stopPropagation(); onPrev(); }} className="absolute left-4 top-1/2 -translate-y-1/2 text-white hover:text-gray-300 p-2 bg-black/20 rounded-full transition-colors">
                        <ArrowLeftIcon className="w-8 h-8" />
                    </button>
                    <button onClick={(e) => { e.stopPropagation(); onNext(); }} className="absolute right-4 top-1/2 -translate-y-1/2 text-white hover:text-gray-300 p-2 bg-black/20 rounded-full transition-colors">
                        <ChevronRightIcon className="w-8 h-8" />
                    </button>
                    <div className="relative">
                        <img src={imageUrl} alt="Enlarged bakery item" className="max-h-[90vh] max-w-[90vw] object-contain rounded-lg shadow-2xl" onClick={e => e.stopPropagation()} />
                    </div>
                </>
            )}
        </div>
    );
};
